@import url(https://fonts.googleapis.com/css?family=Assistant:300,400,600,700&display=swap&subset=hebrew);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Assistant', 'Courier New', sans-serif, monospace;
}

*{color:#3d3d3d;box-sizing:border-box}html,body{width:100%;margin:0;padding:0;font-size:12px;font-weight:normal;box-sizing:border-box;background-color:#FFF8F2}.App{direction:rtl;width:100%;overflow:auto}strong{font-weight:60%}ul,li{list-style-type:none;margin:0;padding:0;border:0;outline:0}button{background-color:#FFF8F2}

.Header{width:100%;padding:10px;display:flex;justify-content:space-between}.Header img{float:right;max-height:40px}.Header sub{font-size:11px}.small-logo{display:none;width:45px}.large-logo{display:block}@media (max-width: 768px){.Header{width:100%;padding:0;margin:0}.small-logo{display:block}.large-logo{display:none}}

.Main{margin-top:1em;margin-bottom:1em;background-color:#f1dfcf;font-size:48px;line-height:1.1;width:100%;height:3.5em}.Main .container{display:flex;justify-content:space-between;align-items:center;width:660px;margin:0 auto}.Main .container img{width:220px;align-self:flex-end}@media (max-width: 540px){.Main{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;font-size:30px}.Main .container{width:100%;flex-direction:column;align-items:center}.Main .container img{align-self:unset}}

.Description{margin:5em;font-size:0.7em}.Description .container{background-color:#f1dfcf;width:660px;margin:auto;padding:1.5em}.Description .container p:last-child{margin:0;padding:0}@media only screen and (max-width: 768px){.Description{width:80%;margin-left:auto;margin-right:auto}.Description .container{width:100%}}

.DealPack{background-color:#FFF8F2;min-width:300px;border:solid 1px #d4d4d4;border-radius:4px;position:relative;text-align:center;padding-bottom:25px;margin:10px;height:520px;line-height:1}.DealPack>*{margin-bottom:10%}.DealPack .top{border:solid 1.5px #fd3d3d;margin-top:-20px}.DealPack .topHead{position:absolute;border-radius:4px 4px 0 0;background-color:#fd3d3d;top:-19px;left:-1px;color:#fdcfcf;width:300px;height:20px;padding-top:2.5px;font-weight:500}

.PackPrice{display:flex;flex-wrap:nowrap;width:150px;font-size:48px;margin-left:auto;margin-right:auto;align-items:center}.PackPrice .price:after{content:"";padding:25px 1px;border-right:1px solid #979797;transform:rotate(20deg);position:absolute;right:135px}.PackPrice .period{font-size:16px;padding-right:10px;display:inline-block;max-width:50px;line-height:1;padding-right:30px}

.PackShortTerms{display:flex;align-content:flex-start}

.PackButton{width:60%;color:#fd3d3d;border:1px solid #fd3d3d;border-radius:2px;height:35px;margin-top:10px;margin-bottom:10px}.topBtn{width:50%;background-color:#fd3d3d;color:#fdcfcf;border:1px solid #fd3d3d;border-radius:2px;height:40px;font-weight:500}

.PackDetails{text-align:right;list-style:none;max-width:220px;margin:12px 50px 20px 20px;font-size:13px}.PackDetails li{position:relative;padding:10px 10px 8px 0}.PackDetails li:before{content:url(/globs-app/static/media/check-grey.c0c24617.svg);position:absolute;top:0;right:-20px}.toplist li:before{content:url(/globs-app/static/media/check-red.bd68e58e.svg)}

.PackLongTerms{position:relative;left:60px;bottom:-60px}.PackLongTerms:before{content:"";position:absolute;border-top:1px solid #d4d4d4;width:80%;text-align:center;left:-30px;top:-15px}.PackLongTerms:after{content:"";border:solid #1a1a1a;border-width:0 1px 1px 0;padding:3px;transform:rotate(45deg);transition:transform 0.3s ease;position:absolute;left:100px}

.DealMenu{margin-bottom:5em;display:flex;flex-wrap:wrap;align-items:center;align-content:center;justify-content:center}@media only screen and (max-width: 768px){.DealMenu{flex-direction:column}}

